import React from "react";
import { NavLink } from "react-router-dom";
import "../styles/footer.css";

const Footer = () => {
  return (
    <div class="footer">
      <div class="footer-pages footer-pages-col-1">
        <ul>
          <li>
            <NavLink class="link-li" to="reprezentanci">
              Reprezentanci
            </NavLink>
          </li>
          <li>
            <NavLink class="link-li" to="postulaty">
              Postulaty
            </NavLink>
          </li>
          <li>
            <NavLink class="link-li" to="kontakt">
              Kontakt
            </NavLink>
          </li>
          <li>
            <NavLink class="link-li" to="feedback">
              Zgłoś błąd lub sugestię
            </NavLink>
          </li>
        </ul>
      </div>
      <div class="footer-pages footer-pages-col-2">
        <ul>
          <li>
            <NavLink class="link-li" to="informacje_o_stronie">
              Informacje o stronie
            </NavLink>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Footer;
