import React from "react";
import "../styles/PagesStyles/LoginPage.css";

const LoginPage = () => {
  return (
    <div class="loginPage">
      <p>
        Rejestrować i logować można się za pomocą swojego konta na facebooku lub
        konta google+. Tak jest najprościej.
        <br />
        <br />
        Oprócz tego można się zarejestrować, a potem logować, poprzez formularz
        na stronie – podając jedynie swój email (nie będzie widoczny dla innych
        użytkowników) oraz preferowaną nazwę użytkownika. Hasło do zalogowania
        zostanie wysłane na podany email – a następnie, po pierwszym zalogowaniu
        można ustawić własne hasło klikając w menu w „edytuj profil”.
        <br />
        <br />
        Indywidualne ikonki użytkowników są pobierane z konta na facebooku,
        google+ lub z serwisu gravatar – w zależności od metody rejestracji.{" "}
        <br />
        <br /> Dla celów zabezpieczenia przed inwazją botów i atakami sabotażu –
        rejestracja i logowanie poprzez formularz na stronie są zabezpieczone za
        pomocą „capcha” (trzeba „udowodnić”, że nie jest się botem) – proszę
        wybaczyć i zrozumieć tą minimalną niedogodność.
      </p>
    </div>
  );
};

export default LoginPage;
