import React from "react";
import "../../styles/HomePageStyles/poll.css";
//

const Poll = () => {
  return (
    <div class="poll">
      <div class="poll-container">
        <div class="poll-header">Głosowanie na dziś</div>
        <div class="poll-question-box">
          <div class="poll-question">
            Czy uważasz, że zagraniczne korporacje powinny płacić podatki w
            Polsce?
          </div>
          <div class="poll-answers-box">
            <div class="poll-answer poll-answer-yes">TAK</div>
            <div class="poll-answer poll-answer-no">NIE</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Poll;
